import {authAPI, authAPIv4, instanceV4 } from "../api/api"
import Cookie from 'universal-cookie';

const SET_AUTH = 'SET_AUTH'
const SET_LOGIN = 'SET_LOGIN'
const SET_TOKEN = 'SET_TOKEN'
const SET_ACCESS_TOKEN = 'SET_ACCESS_TOKEN'
const SET_SESSION_ID = 'SET_SESSION_ID'
// const SEARCH_TEXT = 'SEARCH_TEXT'
const cookie = new Cookie();
const initialState = {
	login: null,
	password: null,
    token: null,
	loginData: null,
	tokenV4: null,
	accessToken: null,
	accountId: null, 
	sessionId: null, 
}
const authReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_AUTH:
			return { ...state, login: action.login, password: action.password, token: action.token }
		case SET_LOGIN:
			return { ...state, loginData: action.data }
		case SET_TOKEN:
			return { ...state, tokenV4: action.token }
		case SET_ACCESS_TOKEN:
			return { ...state, accessToken: action.token,  accountId: action.id}
		case SET_SESSION_ID:
			return { ...state, sessionId: action.sessionId }
		default:
			return state;
	}
}
export const setAuthActionCreator = (login, password, token) => {
	return {
		type: SET_AUTH,
		login,
        password,
        token
	}
}
export const setLoginData = (data) => {
	return {
		type: SET_LOGIN,
		data,
	}
}
export const setTokenV4Ac = (token) => {
	return {
		type: SET_TOKEN,
		token,
	}
}
export const setSessionIdAc = (sessionId) => {
	return {
		type: SET_SESSION_ID,
		sessionId,
	}
}
export const setAccessTokenAc = (token, id) => {
	return {
		type: SET_ACCESS_TOKEN,
		token,
		id
	}
}
export const setAuth = (querry) => {
	return (dispatch) => {
		// 	authAPI.getToken().then(data1 => {				
		// 		authAPI.getAuth('straksi', 'Myn1995', data1.data.request_token).then(data2=>{					
		// 			authAPI.getSession(data2.request_token).then(data3=>{
		// 				// console.log(data3.data.session_id)
		// 				authAPI.getLogin(data3.data.session_id).then(data4=>{
		// 					dispatch(setLoginData(data4.data))
		// 				})
		// 			})
		// 		})
		// })
		// if(cookie.get('access_token') && cookie.get('account_id')){
		// 	dispatch(setAccessTokenAc(cookie.get('access_token'), cookie.get('account_id') )
		// }
		if(cookie.get('access_token') && cookie.get('account_id')){
			dispatch( setAccessTokenAc(cookie.get('access_token'), cookie.get('account_id') ) )
			authAPI.getSessionId(cookie.get('access_token')).then(sessionData=>{	
				dispatch(setSessionIdAc(sessionData.data.session_id))
				authAPI.getLogin(sessionData.data.session_id).then(loginData=>{
					dispatch(setLoginData(loginData.data))
				})
			})
		}else{
			authAPIv4.getToken().then(data1 => {
				const min =  new Date(Date.now()+ (1000*60*10))
				cookie.set('request_token', data1.request_token,{expires: min, path: '/' })
				if(cookie.get('request_token')){
					window.location.replace('https://www.themoviedb.org/auth/access?request_token=' + data1.request_token )
					// window.open('https://www.themoviedb.org/auth/access?request_token=' + data1.request_token )
					dispatch(setTokenV4Ac(data1.request_token))
				}
				
			})
		}	
	}
}
export const setToken = (token) => {
	return (dispatch) => {		
		// console.log(token)
		authAPIv4.getAccessToken(token).then(data=>{
			dispatch(setAccessTokenAc(data.access_token, data.account_id))
			const day =  new Date(Date.now()+ (1000*60*60*24))
			cookie.set('access_token', data.access_token,{expires: day, secure: true,  path: '/' })
			cookie.set('account_id', data.account_id,{expires: day, secure: true, path: '/' })
			authAPI.getSessionId(data.access_token).then(sessionData=>{	
				dispatch(setSessionIdAc(sessionData.data.session_id))
				authAPI.getLogin(sessionData.data.session_id).then(loginData=>{
					dispatch(setLoginData(loginData.data))
				})
			})
			if (instanceV4.defaults.headers['Authorization'] !== `Bearer ${cookie.get('access_token')}`) {
				instanceV4.defaults.headers['Authorization'] = `Bearer ${cookie.get('access_token')}`;			
			}
		})
	}
}
export const logOut = (sessionId) => {
	return (dispatch) => {		
		// console.log(token)
		authAPI.logOut(sessionId).then(sessionData=>{
			dispatch(setLoginData(null))
			dispatch(setTokenV4Ac(null))
			dispatch(setAccessTokenAc(null, null))
			if (instanceV4.defaults.headers['Authorization'] === `Bearer ${cookie.get('access_token')}`) {
				instanceV4.defaults.headers['Authorization'] = `Bearer ${process.env.REACT_APP_API_KEY_V4}`				
			}
			if(cookie.get('access_token')){
				cookie.remove('access_token', { path: '/' })
			}
			if(cookie.get('account_id')){
				cookie.remove('account_id', { path: '/' })
			}
		})
	}
}
export default authReducer