import {discoverAPI, genresAPI } from "../api/api"

const SET_MOVIES_DATA = 'SET_MOVIES_DATA'
const SET_MOVIES_GENRES = 'SET_MOVIES_GENRES'
const SET_ACTIVE_MOVIES_GENRES = 'SET_ACTIVE_MOVIES_GENRES'
const SET_TV_DATA = 'SET_TV_DATA'
const CURRENT_MOVIES_PAGE = 'CURRENT_MOVIES_PAGE'
const CURRENT_TV_PAGE = 'CURRENT_TV_PAGE'
const SET_TV_GENRES = 'SET_TV_GENRES'
const SET_ACTIVE_TV_GENRES = 'SET_ACTIVE_TV_GENRES'

const initialState = {
	moviesData: null,
	moviesGenres: null,
	moviesActiveGenres: {string: '', array: []},
    tvData: null,
	tvGenres: null,
	tvActiveGenres: {string: '', array: []},
    moviesPage: 1,
	tvPage: 1,
}
const detailsReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_MOVIES_DATA:
			return { ...state, moviesData: {...action.data, total_pages: action.data.total_pages>500?500:action.data.total_pages} }
        case SET_TV_DATA:
            return { ...state, tvData: {...action.data, total_pages: action.data.total_pages>500?500:action.data.total_pages} }
        case CURRENT_MOVIES_PAGE:
            return { ...state, moviesPage: action.page }
        case CURRENT_TV_PAGE:
            return { ...state, tvPage: action.page }
		case SET_MOVIES_GENRES:
			return { ...state, moviesGenres: action.genres }
		case SET_ACTIVE_MOVIES_GENRES:
			if(action.genre!==''){
				if(state.moviesActiveGenres.array.length === 0 || (state.moviesActiveGenres.array.length > 0 && !state.moviesActiveGenres.array.some(genre=>genre===action.genre)) ){
					return { ...state, moviesActiveGenres: {string: [...state.moviesActiveGenres.array, action.genre].join(','), array: [...state.moviesActiveGenres.array, action.genre]} }
				}else{
					return {...state, moviesActiveGenres: {string: state.moviesActiveGenres.array.filter(genre=>genre!==action.genre).join(','), array: state.moviesActiveGenres.array.filter(genre=>genre!==action.genre)}}
				}
			}else{
				return {...state, moviesActiveGenres: {string: '', array: []}}
			}
		case SET_ACTIVE_TV_GENRES:
			if(action.genre!==''){
				if(state.tvActiveGenres.array.length === 0 || (state.tvActiveGenres.array.length > 0 && !state.tvActiveGenres.array.some(genre=>genre===action.genre)) ){
					return { ...state, tvActiveGenres: {string: [...state.tvActiveGenres.array, action.genre].join(','), array: [...state.tvActiveGenres.array, action.genre]} }
				}else{
					return {...state, tvActiveGenres: {string: state.tvActiveGenres.array.filter(genre=>genre!==action.genre).join(','), array: state.tvActiveGenres.array.filter(genre=>genre!==action.genre)}}
				}
			}else{
				return {...state, tvActiveGenres: {string: '', array: []}}
			}	
		case SET_TV_GENRES:
			return { ...state, tvGenres: action.genres }

        default:
            return state;
	}
}
export const setMoviesDataACtionCreator = (data) => {
	return {
		type: SET_MOVIES_DATA,
		data,
	}
}
export const setTvDataActionCreator = (data) => {
	return {
		type: SET_TV_DATA,
		data,
	}
}
export const setMoviesPageActionCreator = (page) => {
	return {
		type: CURRENT_MOVIES_PAGE,
		page,
	}
}
export const setTvPageActionCreator = (page) => {
	return {
		type: CURRENT_TV_PAGE,
		page,
	}
}
export const setMoviesGenresAC = (genres) => {
	return {
		type: SET_MOVIES_GENRES,
		genres,
	}
}
export const setActiveMoviesGenresAC = (genre) => {
	return {
		type: SET_ACTIVE_MOVIES_GENRES,
		genre,
	}
}
export const setActiveTvGenresAC = (genre) => {
	return {
		type: SET_ACTIVE_TV_GENRES,
		genre,
	}
}
export const setTvGenresAC = (genres) => {
	return {
		type: SET_TV_GENRES,
		genres,
	}
}	
export const setDiscoverData = (type, page, genres) => {
	return (dispatch) => {
        discoverAPI.getDiscoverData(type, page, genres).then(data => {
            if(type==='movie'){
                dispatch(setMoviesDataACtionCreator(data))
            }else if(type==='tv'){
                dispatch(setTvDataActionCreator(data))
            }          
		})
		
	}
}
export const setGenresData = (type) => {
	return (dispatch) => {
        genresAPI.getGenres(type).then(data => {
            if(type==='movie'){
                dispatch(setMoviesGenresAC(data.genres))
            }else if(type==='tv'){
                dispatch(setTvGenresAC(data.genres))
            }          
		})
		
	}
}

export default detailsReducer