import {myListsAPI } from "../api/api"

const SET_LISTS = 'SET_LISTS'
const SET_LIST_DETAILS = 'SET_LIST_DETAILS'
const SET_ALL_LISTS_DETAILS = 'SET_ALL_LISTS_DETAILS'
const CLEAN_ALL_LISTS_DETAILS = 'CLEAN_ALL_LISTS_DETAILS'
const SET_ITEMS_TO_REMOVE = 'SET_ITEMS_TO_REMOVE'
const DELETE_ITEMS_TO_REMOVE = 'DELETE_ITEMS_TO_REMOVE'
const CLEAN_ITEMS_TO_REMOVE = 'CLEAN_ITEMS_TO_REMOVE'
const SET_REMOVE_ITEMS_MODE = 'SET_REMOVE_ITEMS_MODE'

const initialState = {
	lists: null,
    listPage: 1,
    listDetails: null,
	allListsDeails: [],
	itemsToRemove: {
		'items':[]
	},
	removeItemsMode: false
}
const listsReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_LISTS:
			return { ...state, lists: action.lists }
        case SET_LIST_DETAILS:
            return { ...state, listDetails: action.data }
		case SET_ALL_LISTS_DETAILS:
			return { ...state, allListsDeails: [ ...state.allListsDeails, {...action.data}] }
		case CLEAN_ALL_LISTS_DETAILS:
			return { ...state, allListsDeails: [] }
		case SET_ITEMS_TO_REMOVE:
			return { ...state, itemsToRemove: {'items': [...state.itemsToRemove.items,{'media_type': action.media_type, "media_id":action.id} ]} }
		case DELETE_ITEMS_TO_REMOVE:
			return { ...state, itemsToRemove: {'items': state.itemsToRemove.items.filter( item=>!(item.media_id===action.id && item.media_type===action.media_type)  ) } }
		case CLEAN_ITEMS_TO_REMOVE:
			return { ...state, itemsToRemove:{'items': []} }
		case SET_REMOVE_ITEMS_MODE:
			return { ...state, removeItemsMode: action.boolean }
		default:
			return state;
	}
}
export const setListsActionCreator = (lists) => {
	return {
		type: SET_LISTS,
		lists
	}
}
export const setListDetailsActionCreator = (data) => {
	return {
		type: SET_LIST_DETAILS,
		data
	}
}
export const setAllListsDetailsActionCreator = (data) => {
	return {
		type: SET_ALL_LISTS_DETAILS,
		data
	}
}
export const cleanAllListsDetailsAc = () => {
	return {
		type: CLEAN_ALL_LISTS_DETAILS,
	}
}
export const setItemsToRemoveAc = (media_type, id) => {
	return {
		type: SET_ITEMS_TO_REMOVE,
		media_type,
		id
	}
}
export const deleteItemsToRemoveAc = (media_type, id) => {
	return {
		type: DELETE_ITEMS_TO_REMOVE,
		media_type,
		id
	}
}
export const cleanItemsToRemoveAc = () => {
	return {
		type: CLEAN_ITEMS_TO_REMOVE,
	}
}
export const setRemoveItemsModeAc = (boolean) => {
	return {
		type: SET_REMOVE_ITEMS_MODE,
		boolean
	}
}
export const setLists = (accoundId) => async (dispatch) => {
	// console.log(store.getState())
	myListsAPI.getLists(accoundId).then(data => {				
		dispatch(setListsActionCreator(data.results))	
	})		
	
}
export const setListDetails = (listId) => {
	return (dispatch) => {
        myListsAPI.getListDetails(listId).then(data => {				
			dispatch(setListDetailsActionCreator(data))	
		})		
	}
}
export const setAllListsDetails = (listId) => {
	return (dispatch) => {
        myListsAPI.getListDetails(listId).then(data => {				
			dispatch(setAllListsDetailsActionCreator(data))	
		})		
	}
}
export const addToList = (listId, mediaType, mediaId, accoundId) => {
	return (dispatch, getState) => {
        myListsAPI.addToList(listId, mediaType, mediaId).then(()=>{
			dispatch(cleanAllListsDetailsAc())
			dispatch(setAllListsDetails(listId))
			dispatch(setLists(accoundId))
		})
	}
}
export const removeFromList = (listId,items) => {
	return (dispatch, getState) => {
        myListsAPI.removeFromList(listId, items).then(()=>{
			dispatch(cleanItemsToRemoveAc())
			dispatch(setRemoveItemsModeAc(false))
			dispatch(setListDetails(listId))
		})
	}
}
export default listsReducer
