import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { compose } from 'redux';
import logo from '../img/svg-logo.svg'
import logoMoblie from '../img/logo-m.png'
import SearchContainer from "./Search/SearchContainer";
import { setAuth, setToken, setTokenV4Ac, logOut } from "../redux/auth-reducer";
import { loginAvatar } from "../global/config";
const Nav = ({accessToken, loginData, setAuth, logOut, sessionId, menuActiveHandler, menuActive, searchShow, searchShowHandler}) => {
	// State of our Menu
	return (
		<nav className="nav">
			<div className="container-fluid">
				<div className="nav-w">
					<div className={`nav-burger ${menuActive ? 'is-close': ''}`} onClick={menuActiveHandler}>
						<span></span>
					</div>
					<SearchContainer searchShow={searchShow} searchShowHandler={searchShowHandler}/>
					<Link to="/" className="nav-logo">
						<img src={logo} alt="" />
						<img className="nav-logo__mobile" src={logoMoblie} alt="" />
					</Link>
					<div className="nav-auth">
						{ !loginData &&
						// <Link to='/permission' className="btn">Sign In from tmdb</Link>
							<button className="btn" onClick={setAuth}>Sign In from tmdb</button>
						}
						{(loginData ) &&
							<div className="nav-login">
								<div className="nav-login__avatar">
									<div>{ loginData.avatar.gravatar.hash && <img src={`${loginAvatar}${loginData.avatar.gravatar.hash}.png`} alt="" />}</div>
								</div>
								<div className="nav-login__body">
									<div className="nav-login__name">{loginData.name || loginData.username}</div>
									<div className="btn" onClick={()=>logOut(sessionId)}>logOut</div>
								</div>
							</div>
						}
					</div>
				</div>
			</div>
		</nav>
	);
};
const mapStateToProps = (state) => {
    return {
        accessToken: state.auth.accessToken,
		login: state.auth.login,
		loginData: state.auth.loginData,
		sessionId: state.auth.sessionId,
    }
}
export default compose(
    connect(mapStateToProps, { setAuth, setToken, setTokenV4Ac, logOut }),
)(Nav)
