import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
// import { withRouter } from 'react-router-native';
import { compose } from 'redux';
import { setSearchResults, setSearchTextActionCreator} from "../../redux/search-reducer";
import { setAuth} from "../../redux/auth-reducer";
import Search from './Search';
// debugger

const SearchContainer = (props) => {

    return (
        <Search setSearchResults={props.setSearchResults} setSearchText={props.setSearchTextActionCreator} searchText={props.search.searchText} searchShowHandler={props.searchShowHandler} searchShow={props.searchShow}/>
        // <button  onClick={()=>props.setSearchResults('spider')}>sdasd</button>
    );
};
const mapStateToProps = (state)=>{
    return{
        search: state.search,
        login: state.auth.loginData,
    }
}
export default compose( 
    connect(mapStateToProps, {setSearchResults, setSearchTextActionCreator, setAuth})
)(SearchContainer)