import React, { useEffect, useRef, useState } from 'react';
import Sidebar from './Sidebar';
import { gsap } from "gsap";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
const genresData = [
	{
		_id: '01',
		genreName: 'Action & Adventure',
		filmName: 'The 100',
		headerImg: `${process.env.PUBLIC_URL}/img/100.jpg`,
		position: { top: '0px', bottom: '80px', class: 'is-top' }
	},
	{
		_id: '02',
		genreName: 'Anime',
		filmName: 'Castlevania',
		headerImg: `${process.env.PUBLIC_URL}/img/Castlevania.jpg`,
		position: { top: '40px', bottom: '40px', class: 'is-middle' }
	},
	{
		_id: '03',
		genreName: 'Thriller',
		filmName: 'BreakingBad',
		headerImg: `${process.env.PUBLIC_URL}/img/BreakingBad.jpg`,
		position: { top: '80px', bottom: '0px', class: 'is-bottom' }
	},
	{
		_id: '04',
		genreName: 'Sci-Fi & Fantasy',
		filmName: 'Interstellar',
		headerImg: `${process.env.PUBLIC_URL}/img/interstellar.jpg`,
		position: { top: '40px', bottom: '40px', class: 'is-middle' }
	},
	{
		_id: '05',
		genreName: 'Dramas',
		filmName: 'peaky',
		headerImg: `${process.env.PUBLIC_URL}/img/peaky.jpg`,
		position: { top: '0px', bottom: '80px', class: 'is-top' }
	},
	{
		_id: '06',
		genreName: 'Crime',
		filmName: 'Casa de papel',
		headerImg: `${process.env.PUBLIC_URL}/img/castle.jpg`,
		position: { top: '80px', bottom: '0px', class: 'is-bottom' }
	},
	{
		_id: '07',
		genreName: 'horror',
		filmName: 'marianne',
		headerImg: `${process.env.PUBLIC_URL}/img/marianne.jpg`,
		position: { top: '40px', bottom: '40px', class: 'is-middle' }
	},
	{
		_id: '08',
		genreName: 'History',
		filmName: 'Sparta',
		headerImg: `${process.env.PUBLIC_URL}/img/sparta.jpg`,
		position: { top: '0px', bottom: '80px', class: 'is-top' }
	},
]
const Header = ({menuActive}) => {
	const genreWidth = `100%/${genresData.length}`
	const genreWidthBox = `(100% - ${(genresData.length - 1) * 20}px)/${genresData.length}`
	const hoverGenreWidth = `100%/(${genresData.length} - 2)`
	const [genreData, setGenreData] = useState({ name: null, id: null })
	const [genreActiveData, setGenreActiveData] = useState({ name: null, id: null })
	const [genresWidth, setGenresWidth] = useState(0)
	const genreRef = useRef([])
	genreRef.current = []
	const [content, setContent] = useState([])
	useEffect(() => {
		// trendingAPI.getTrendings().then(data=>{
		// 	console.log(data)
		// })
	}, [])

	const addToRefs = (el, id) => {
		if (el && !genreRef.current.includes(el)) {
			if (id >= genreData.id && id <= genresData.length && !el.classList.contains('is-active') && genreData.name !== null) {
				genreRef.current.push(el)
				el.classList.add('is-small')
			} else {
				el.classList.remove('is-small')
			}
			// console.log(genreRef.current)
		}
	}
	const onHoverHandler = (e, id) => {
		setGenreData({ name: e.target.dataset.genre, id: id })
		// e.target.classList.add('is-hover')
		// console.log(e, id)
	}
	const onActiveHandler = (e, id) => {

		if (genreData.id === id) {
			setGenreActiveData({ name: e.target.dataset.genre, id: id })
		} else {

		}
	}
	const onHLeaveHandler = (e) => {
		// console.log('убрал')
		// e.target.classList.remove('is-hover')
		setGenreData({ name: null, id: null })
	}
	// GSAP Animation 
	const el = useRef();
	const q = gsap.utils.selector(el);
	const sidebarRef = useRef()
	const sideBarSelector = gsap.utils.selector(sidebarRef);
	const [menuTl] = useState(gsap.timeline({ paused: true }));
	const animateHandle = () => {
		gsap.to(q(".genre-box"), {
			height: '100%',
			stagger: .15,
			yoyo: true,
			duration: 1,
			ease: "Power2.easeOut",
		});
		gsap.from(q(".genre p"), {
			opacity: 0,
			stagger: .25,
			yoyo: true,
			duration: .8,
			//   ease:"Power2.easeOut",
		});
	}
	useEffect(() => {
		animateHandle()
		
		let totalWidth = 0;
		el.current.childNodes[2].childNodes.forEach(item=>{
			// console.log(item.getBoundingClientRect().width)
			totalWidth = totalWidth + item.getBoundingClientRect().width
		})
		setGenresWidth(totalWidth)
		menuTl.to(sideBarSelector("li"), {
			opacity: 0,
			stagger: .1,
			duration: 0.6,
			ease: "Power2.easeOut",
		})
			.reverse();
		// menuTl.reversed(!menuTl.reversed());
	}, []);
	const toggleMenuTimeline = () => {
		menuTl.reversed(!menuTl.reversed());
	};
	return (
		<header className="header">
			<div className="container-fluid">
				{/* <button onClick={toggleMenuTimeline}>click</button> */}
				{/* <button onClick={onActiveHandler}>click</button> */}
				<div className="header-w">
					<Sidebar menuActive={menuActive} ref={sidebarRef} />
					<div className="header-slider">
						<Swiper
							slidesPerView={'auto'}
							navigation
							className="genres"
							style={{ marginRight: '-20px' }}
							ref={el}
						>
							{genresData.map((genre, id) => {
								return <SwiperSlide key={genre._id} className={'genre ' + genre.position.class + (id === genreActiveData.id ? ' is-active' : '') + (id === genreData.id ? ' is-hover' : '')} style={{ width: `calc(${genreWidth})` }} data-genre={genre.genreName}
									onMouseEnter={el => onHoverHandler(el, id)} onMouseLeave={onHLeaveHandler}
									onClick={(el => onActiveHandler(el, id))}
								>
									<div className="genre-box">
										<img src={genre.headerImg} alt=""
											style={{ left: `calc(-${id}*${genresWidth / genresData.length}px )`, width: genresWidth }}
										/>
									</div>
									<p>{genre.genreName}</p>
								</SwiperSlide>
							})}
						</Swiper>
					</div>
				</div>
			</div>
		</header>
	);
};

export default Header;