const SET_INITIAL_STATE = 'SET_INITIAL_STATE';
const SET_BODY_OVERFLOW = 'SET_BODY_OVERFLOW';

const initialState = {
	initialState: false,
	bodyOverflow: false,
}
const appReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_INITIAL_STATE:
			return { ...state, initialState: true }
		case SET_BODY_OVERFLOW:
			return { ...state, bodyOverflow: action.bodyOverflow }
		default:
			return state;
	}
}
export const setInitialStateCreator = () => ({ type: SET_INITIAL_STATE })
export const setBodyOverflow = (bodyOverflow) => ({ type: SET_BODY_OVERFLOW, bodyOverflow })
