import React from 'react';
import { NavLink } from 'react-router-dom';

const Sidebar = React.forwardRef((props, sidebarRef) => {
    return (
        <ul className={`sidebar ${props.menuActive ? 'is-active': ''}`} ref={sidebarRef}>
            <li><NavLink to="/">home</NavLink></li>
            <li><NavLink to="/tv" >tv shows</NavLink></li>
            <li><NavLink to="/movies" >moveis</NavLink></li>
            <li><a href="s" >latest</a></li>
            <li><NavLink to="/lists" >my list</NavLink></li>   
        </ul>
    );
});

export default Sidebar;