import React from 'react';
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom"
import { toElementScroll } from '../../global/config';
import searhIcon from '../../img/svg-search.svg'
const Search = ({setSearchResults, setSearchText, searchText, searchShow, searchShowHandler}) => {
	const navigate = useNavigate()
	const { register, handleSubmit, watch, formState: { errors } } = useForm();
	// debugger
	const onSubmit = data => {
		if(data.search!==''){
			setSearchResults(data.search);
			setSearchText(data.search)
			navigate(`/search?s=${data.search}`)
			// console.log('sd')
			// document.querySelector('section').scrollIntoView({ 
			// 	behavior: 'smooth', 
			// 	block: 'start' 
			// })
			toElementScroll(true)
		}
	}
	return (
		<div className="nav-search">
			<div className="nav-search__show" onClick={searchShowHandler}><img src={searhIcon} alt="" /></div>
			<form className={`form ${searchShow ? 'is-show': ''}` } onSubmit={handleSubmit(onSubmit)}>
				<div className="nav-search__close" onClick={searchShowHandler}>
					<svg width="20" height="20" viewBox="0 0 20 20" focusable="true" fill="white"><path d="M10.5 9.3L1.8 0.5 0.5 1.8 9.3 10.5 0.5 19.3 1.8 20.5 10.5 11.8 19.3 20.5 20.5 19.3 11.8 10.5 20.5 1.8 19.3 0.5 10.5 9.3Z"></path></svg>
				</div>
				{/* register your input into the hook by invoking the "register" function */}
				<div className="form__search">
					<div className="form__group">
						<input className="form__input" name="search" id="search" required {...register("search") }
							// onKeyUp={handleSubmit(onSubmit)}                       
						 />
						 <label htmlFor="search" className="form__label">Search</label>
						<span className="form__border"></span>
					</div>
					<button type="submit"><img src={searhIcon} alt="" /></button>
					
				</div>
				{/* {errors.exampleRequired && <span>This field is required</span>} */}

				
			</form>
		</div>
	);
};

export default Search;