import { searchAPI } from "../api/api"

const SEARCH_R = 'SEARCH_R'
const SEARCH_TEXT = 'SEARCH_TEXT'
const CURRENT_PAGE = 'CURRENT_PAGE'

const initialState = {
	searchData: [],
	searchText: null,
	totalPages: 0,
	page: 1,
}
const searchReducer = (state = initialState, action) => {
	switch (action.type) {
		case SEARCH_TEXT:
			return { ...state, searchText: action.text }
		case SEARCH_R:
			return { ...state, searchData: action.result, totalPages: action.total }
		case CURRENT_PAGE:
			return { ...state, page: action.page }
		default:
			return state;
	}
}
export const setSearchTextActionCreator = (text) => {
	return {
		type: SEARCH_TEXT,
		text
	}
}
export const searchActionCreator = (result, total) => {
	return {
		type: SEARCH_R,
		result,
		total
	}
}
export const setPageActionCreator = (page) => {
	return {
		type: CURRENT_PAGE,
		page,
	}
}
