import React, { useEffect, useState } from "react";
import { Route, Routes, useLocation } from 'react-router-dom'
import {AnimatePresence} from 'framer-motion/dist/framer-motion'
// import { withRouter } from 'react-router-native';
import Nav from "./components/Nav";
import Header from "./components/Header";
import Footer from "./components/Footer";
import { connect } from 'react-redux';
import { setAuth } from "./redux/auth-reducer";
import './sass/style.sass';
import Cookie from 'universal-cookie';
import Preloader from "./components/Preloader";
import { compose } from "redux";
const cookie = new Cookie();
const SearchPage = React.lazy(() => import('./Pages/SearchPage'));
const DetailsPage = React.lazy(() => import('./Pages/DetailsPage'));
const MoviePage = React.lazy(() => import('./Pages/MoviePage'));
const TvPage = React.lazy(() => import('./Pages/TvPage'));
const PermissionPage = React.lazy(() => import('./Pages/PermissionPage'));
const ListsPage = React.lazy(() => import('./Pages/ListsPage'));
const ListPage = React.lazy(() => import('./Pages/ListPage'));
function App(props) {
	
	const location = useLocation()
	const [menuActive, setMenuActive] = useState(false)
	const [searchShow, setSearchShow] = useState(false)
	useEffect(() => {
		if (cookie.get('access_token')) {
			props.setAuth()
		}
	}, [])
	useEffect(() => {
		if (location.pathname === '/') {
			// console.log('sdsd')
			document.title = 'Netflix'
		}
	}, [props.match])
	const menuActiveHandler = () =>{
		setMenuActive(prev=>!prev)
	}
	const searchShowHandler = () =>{
        setSearchShow(prev=>!prev)
    }
	useEffect(() => {
		if (menuActive) {
			document.querySelector('body').style.overflow = 'hidden'
		}else{
			document.querySelector('body').style.overflow = ''
		}
	}, [menuActive])
	useEffect(() => {
		setMenuActive(false)
		setSearchShow(false)
	}, [location])
	return (
		<>
			<Nav menuActiveHandler={menuActiveHandler} menuActive={menuActive} searchShow={searchShow} searchShowHandler={searchShowHandler}/>
			<Header menuActive={menuActive} />
			<main className="main">
				<React.Suspense fallback={<Preloader />}>
					<AnimatePresence exitBeforeEnter>
						<Routes key={location.pathname} location={location}>
							{/* <Route path='/search' render={() =>
								(<SearchPage />)
							} />
							<Route exact path='/title/:titleType?/:titleId?' render={() =>
								(<DetailsPage />)
							} />
							<Route path='/movies' exact render={() =>
								(<MoviePage title="Фильмы" />)
							} />
							<Route path='/tv' exact render={() =>
								(<TvPage />)
							} />
							<Route path='/lists' exact render={() =>
								(<ListsPage />)
							} />
							<Route path='/lists/:listId?' exact render={() =>
								(<ListPage />)
							} />
							<Route path='/permission' render={() =>
								(<PermissionPage />)
							} /> */}
							<Route path="/search" element={<SearchPage />} />
							<Route path="/title/:titleType/:titleId" element={<DetailsPage />} />
							<Route path="/movies" element={<MoviePage title="Фильмы" />} />
							<Route path="/tv" element={<TvPage />} />
							<Route path="/lists" element={<ListsPage />} />
							<Route path="/lists/:listId" element={<ListPage />} />
							<Route path="/permission" element={<PermissionPage />} />
						</Routes>
					</AnimatePresence>
				</React.Suspense>
			</main>
			<Footer />
		</>
	);
}
const mapStateToProps = (state) => {
	return {
		accessToken: state.auth.accessToken,
	}
}
export default compose(
	connect(mapStateToProps, { setAuth })
)(App)
