import {detailsAPI } from "../api/api"

const SET_DATA = 'SET_DATA'
const SET_TYPE = 'SET_TYPE'

const initialState = {
	data: null,
    titleType: null,
}
const detailsReducer = (state = initialState, action) => {
	switch (action.type) {
		case SET_DATA:
			return { ...state, data: action.data }
        case SET_TYPE:
            return { ...state, titleType: action.titleType }
		default:
			return state;
	}
}
export const setDataACtionCreator = (data) => {
	return {
		type: SET_DATA,
		data,
	}
}
export const setTypeActionCreator = (titleType) => {
	return {
		type: SET_TYPE,
		titleType,
	}
}
export const setData = (id, type) => {
	return (dispatch) => {
		setDataACtionCreator(null)
        detailsAPI.getDetails(id, type).then(data => {
            dispatch(setDataACtionCreator(data))
		})
		
	}
}

export default detailsReducer