export const middleImgPath = 'https://image.tmdb.org/t/p/w500'
export const smallImgPath = 'https://image.tmdb.org/t/p/w300'
export const originalImgPath = 'https://image.tmdb.org/t/p/original'
export const unavailable = "https://www.movienewz.com/img/films/poster-holder.jpg";
export const loginAvatar = 'https://secure.gravatar.com/avatar/'
export const dateConvert = (date)=>{
	let day = date.substr(-2)
	if(day.substr(0,1)==='0'){
		day = day.substr(1,2)
	}
	const year = date.substr(0,4)
	const mounth = date.substr(5,2)
	function mounthToWord(){
		switch (mounth) {
			case '1':
				return 'января'	
			case '2':
				return 'февраля'
			case '3':
				return 'марта'	
			case '4':
				return 'апреля'
			case '5':
				return 'мая'	
			case '6':
				return 'июня'
			case '7':
				return 'июля'	
			case '8':
				return 'августа'
			case '9':
				return 'сентября'	
			case '10':
				return 'октября'
			case '11':
				return 'ноября'	
			case '12':
				return 'декабря'
			default:
				return mounth;
		}
	} 
	return `${day}.${mounth}.${year}`
}
export const toElementScroll = (smooth=false) =>{
	if(document.querySelector('section')){
		document.querySelector('section').scrollIntoView({ 
			behavior: smooth?'smooth': 'auto', 
			block: 'start' 
		})
	}
	
}