import * as axios from "axios"
import Cookie from 'universal-cookie';
const cookie = new Cookie();
const accessToken = cookie.get('access_token') ?? null
const api_key = process.env.REACT_APP_API_KEY;
const api_key_v4 = process.env.REACT_APP_API_KEY_V4;
const myURL = process.env.PUBLIC_URL
const instance = axios.create({
    baseURL: 'https://api.themoviedb.org/3/',
})
const api = axios.create({
    baseURL: "https://api.themoviedb.org/3/",
    params: {
        api_key: api_key,
        // language: "ko-KR"
    }
});
export const instanceV4 = axios.create({
    baseURL: 'https://api.themoviedb.org/4/',
    withCredentials: false,
    headers:{
        Accept: 'application/json, text/javascript, */*; q=0.0',
        'Content-Type': 'application/json;charset=utf-8',
        // 'X-Content-Type-Options': 'nosniff',
        // 'Cache-Control': 'max-age=3600',
        'Authorization': 'Bearer ' + (cookie.get('access_token')===undefined ? api_key_v4: cookie.get('access_token'))
    }
})
export const authAPIv4 = {
    getToken(){
        return instanceV4.post(`auth/request_token`,
        {
            // "redirect_to": "http://localhost:3000"
            "redirect_to": "http://localhost:3000/#/permission",
            // "redirect_to": 'https://d.wmproj.com/html/nfx/permission'
            // "redirect_to": 'https://straksi.github.io/NetflixCopy/#/permission'
        }).then(response=>response.data)
    },
    getAccessToken(token){
        return instanceV4.post(`auth/access_token`,
        {
            "request_token": token
        }).then(response=>response.data)
    },
}
export const myListsAPI = {
    getLists(id){
        return instanceV4.get(`account/${id}/lists?page=1`,
        ).then(response=>response.data)
    },
    getListDetails(id){
        return instanceV4.get(`list/${id}?page=1&language=ru-RU`,
        ).then(response=>response.data)
    },
    addToList(listId, mediaType, mediaId){
        // if(cookie.get('access_token')){
        //     instanceV4.defaults.headers['Authorization'] = `Bearer ${cookie.get('access_token')}`;
        // }       
        // console.log(instanceV4.defaults)
        return instanceV4.post(`list/${listId}/items`,
        {
            "items": [
              {
                "media_type": mediaType,
                "media_id": mediaId
              }
            ]
          }
        )
    },
    removeFromList(listId, items){
        return instanceV4.delete(`list/${listId}/items`, 
        {data:items}
        )
    },
}
