export const trendingAPI = {
    getTrendings(){
        return instance.get(`trending/all/week`).then(response=>response.data)
    },
    getNetflix(){
        return instance.get(`discover/tv&with_networks=123`).then(response=>response.data)
    },
}
export const searchAPI = {
    getSearchResult(querry, page=1, type){
        return instance.get(`search/${type}?api_key=${api_key}&language=ru-RU&query=${querry}}&page=${page}&include_adult=false`).then(response=>response.data)
    }
}
export const detailsAPI = {
    getDetails(id, type='tv'){
        if(type === 'tv'){
            return instance.get(`${type}/${id}?api_key=${api_key}&language=ru-RU&append_to_response=content_ratings,credits,recommendations,videos,images&include_image_language=en%2Cnull`).then(response=>response.data)
        }else if(type ==='movie'){
            return instance.get(`${type}/${id}?api_key=${api_key}&language=ru-RU&append_to_response=releases,credits,recommendations,videos,images&include_image_language=en%2Cnull`).then(response=>response.data)
        }else if(type ==='person'){
            return instance.get(`${type}/${id}?api_key=${api_key}&language=ru-RU&append_to_response=combined_credits,images&include_image_language=en%2Cnull`).then(response=>response.data)
        }
       
    }
}
export const discoverAPI = {
    getDiscoverData(type, page=1, genres=""){
        return instance.get(`discover/${type}?api_key=${api_key}&language=ru-RU&sort_by=popularity.desc&include_adult=false&include_video=false&page=${page}&with_genres=${genres}&with_watch_monetization_types=flatrate`).then(response=>response.data)
    }
}
export const genresAPI = {
    getGenres(type){
        return instance.get(`genre/${type}/list?api_key=${api_key}&language=ru-RU`).then(response=>response.data)
    }
}
